#showcase {
  background: url(/static/media/bg1.b7f5ed9c.jpg) no-repeat center center/cover;
  height: 600%;
}

.text-primary1 {
  color: #da884e;
}

.lead {
  font-size: 20px;
}

#showcase .showcase-content-home {
  color: #fff;
  text-align: center;
  padding-top: 170px;
}

#showcase .showcase-content-home h1 {
  font-size: 60px;
  line-height: 1.2em;
  padding-bottom: 20px;
}

#showcase .showcase-content-home p1 {
  padding-bottom: 20px;
  line-height: 1.7em;
}

.btn-home {
  display: inline-block;
  font-size: 18px;
  color: #fff;
  background: #333;
  padding: 13px 20px;
  border: none;
  cursor: pointer;
}

.btn-home:hover {
  background: #f7c08a;
  color: #333;
}

.btn-home-light {
  background: #f4f4f4;
  color: #333;
}

.bg-dark {
  background: rgb(92, 91, 91);
  color: #fff;
}

.bg-primary {
  background: #f7c08a;
  color: #333;
}

